import React from 'react';
import {  PanelHeader,  Avatar, Div, HeaderButton, List, Cell, Snackbar, Group, InfoRow, Progress, Tabs, TabsItem, FixedLayout} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
var axios = require('axios');



class Questions extends React.Component {
constructor(props){
super(props)

this.state ={
activeTab:'questions'

}
}




	render=(e)=>{
		return(
			<Div>
			<div>
			<FixedLayout vertical="top">
			     <Tabs theme="light">
                <TabsItem
                 onClick={() => this.setState({ activeTab: 'questions' })}
                  selected={this.state.activeTab === 'questions'}
                >
                  Задания
                </TabsItem>
                <TabsItem
                     onClick={() => this.setState({ activeTab: 'rr' })}
                  selected={this.state.activeTab === 'rr'}

                >
                  Рейтинг
                </TabsItem>
          </Tabs>
        </FixedLayout>
				</div>
				<div style={{ paddingTop: 30, paddingBottom: 30}}>
          {this.state.activeTab === "questions" ?<List>
              {this.props.quest.map((name,i)=>
                <Cell key={i}
                      before={<Avatar src={name.img} size={72} />}
                      size="l">{"  "+name.name+" "}</Cell>
              )}
              </List>:<List>
              {this.props.rr.map((name,i)=>
									<Cell key={i}
                      before={<Avatar src={name.img} size={72} />}
                      size="l"><p>{"  "+name.first_name+" "+name.last_name+"  "+name.ball}</p>
											</Cell>

              )}
              </List>}
					</div>
							  <FixedLayout vertical="bottom">
								<Cell before={<Avatar src={this.props.fetchedUser.photo_200} size={72} />}
										size="l"><p>{"  "+this.props.fetchedUser.first_name+" "+this.props.fetchedUser.last_name+"  "+this.props.ball}</p>
										</Cell>
							  </FixedLayout>

              </Div>
			);
	}
}
export default Questions;
