
import React from 'react';
import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api';
import connect from '@vkontakte/vkui-connect';
import {  View, Tooltip, ScreenSpinner, Epic, Button,PanelHeader, Panel, Tabbar, TabbarItem, Avatar,Alert, Div, HeaderButton, Cell, Snackbar, Gallery, Group, Tabs, TabsItem} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Profile from './panels/Profile';
import Modal from './panels/Modal';
import Achievment from './panels/Achievment';
import MapContainer from './panels/MapContainer';
import End from './panels/End'
import Poll from './panels/Poll';
import Begin from './panels/Begin'
import Questions from './panels/Questions';
import Rr from './panels/Rr';

import Icon28MoreHorizontal from '@vkontakte/icons/dist/28/more_horizontal';
import Icon28Place from '@vkontakte/icons/dist/28/place';
import Icon28User from '@vkontakte/icons/dist/28/user';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Replay from '@vkontakte/icons/dist/24/replay';
import Icon28RecentOutline from '@vkontakte/icons/dist/28/recent_outline';
import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon24Share from '@vkontakte/icons/dist/24/share';


var axios = require('axios');


const orangeBackground = {
  backgroundImage: 'linear-gradient(135deg, #ffb73d, #ffa000)'
};

const blueBackground = {
  backgroundColor: 'var(--accent)'
};
 const api = new VKMiniAppAPI();

class App extends React.Component {
  constructor (props) {
    super(props);
    this.num = 0;

    this.state = {
      activeStory: 'load',
      activePanelMap: "map",
      activeTab:'questions',
      header: true,
      snackbar:null,
      popout: null,
      selectedPlace:null,
      fetchedUser: null,
      id:null,
      ball:0,
      tooltip:true,
      patency:true,
      rr:null,
      quest:null,
      a:null,
      questList:null,
      access_token: null,
      geoData: null,
      marker:null,
      errorGeo:null,
      Poll:null,
      PollEnd:null,
      showEnd:false


    };
  }
    componentDidMount(e){
    api.initApp();
    this.setState({popout:<ScreenSpinner />});
    api.getUserInfo().then(userInfo => {
    this.setState({fetchedUser:userInfo,id:userInfo.id});
    axios.post("https://www.questsinpocket.com/vk/index.php?type=create&id_vk="+
                userInfo.id+"&ball=0&first_name="+userInfo.first_name+"&last_name="+
                userInfo.last_name+"&img="+userInfo.photo_200).then(data=>{
      console.log(data.data);
      if(data.data === 2){
        axios.post("https://www.questsinpocket.com/vk/index.php?type=get&id_vk="+userInfo.id).then(data=>{
          this.setState({ball:data.data});
          axios.post("https://www.questsinpocket.com/vk/index.php?type=get_a&id_vk="+userInfo.id).then(data=>{
          this.setState({popout:null, activeStory:'profile',a:data.data});
        })
        })
      }else if(data.data === 1){
          axios.post("https://www.questsinpocket.com/vk/index.php?type=set_a&id_vk="+userInfo.id+"&a="+data.data).then(data=>{
            axios.post("https://www.questsinpocket.com/vk/index.php?type=get_a&id_vk="+userInfo.id).then(data=>{
          this.setState({popout:null,a:data.data,activeStory:'profile',snackbar:
     <Snackbar
        onClose={() => this.setState({ snackbar: null })}
        action="Поделиться"
        onActionClick={() => this.sendHistory()}
        before={<Avatar size={16} style={orangeBackground}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
      >
      Получено достижение
    </Snackbar>});

  })
})

      }
    })

    })


    }

    onClickMap=(e)=>{
      axios.get("https://www.questsinpocket.com/vk/index.php?type=get_all_points")
      .then(data=>{

        this.setState({marker:data.data})});
      this.setState({popout:<ScreenSpinner />})
      api.getGeodata().then(dataGeo=>{
        this.setState({geoData: dataGeo})
        setTimeout(()=>{this.setState({ activeStory:'map', popout:null})},500);
        console.log(dataGeo);
      });

    }

    onClickOther=(e)=>{
      this.setState({ popout:<ScreenSpinner /> });
      axios.get("https://www.questsinpocket.com/vk/index.php?type=get_all_rr")
      .then(data=>{
      this.setState({rr:data.data})
      axios.get("https://www.questsinpocket.com/vk/index.php?type=get_all_quest&id_vk="+this.state.fetchedUser.id)
      .then(data=>{
        this.setState({questList:data.data, activeStory:'other', popout:null})});
     });

      }



    updateGeodata=(e)=>{
      api.getGeodata().then(dataGeo=>{
      this.setState({geoData: dataGeo});
      console.log(dataGeo);
      })
    }

    getPoll(e){
      axios.get("https://www.questsinpocket.com/vk/index.php?type=get&vk_id=0&id="+this.state.selectedPlace.id_marker).then(data=>{
        this.setState({Poll: data.data});
      })

    }

    checkGeo=(a, e)=>{

      this.setState({popout: <ScreenSpinner /> });
      api.getGeodata().then(dataGeo=>{this.setState({geoData:dataGeo})});
      const p1 = (this.state.geoData.lat).toFixed(this.num);
      const m1 = (+a.position.lat).toFixed(this.num);
      const p2 = (this.state.geoData.long).toFixed(this.num);
      const m2 = (+a.position.lng).toFixed(this.num);
      if(p1 === m1 && p2 === m2) {

        axios.get("https://www.questsinpocket.com/vk/index.php?type=check_quest&id_vk="+this.state.fetchedUser.id+"&id_marker="+a.id_marker).then(data=>{
          if(data.data == 10){
            this.setState({patency:false});
          }else {
            this.setState({patency:true});
          }

        axios.get("https://www.questsinpocket.com/vk/index.php?type=get_poll&id_marker="+a.id_marker).then(data=>{
          if(data.data === ""){
            this.setState({snackbar:
              <Snackbar
        onClose={() => this.setState({ snackbar: null })}
        action={<Icon24Share />}
        onActionClick={() => this.setState({ text: 'Добавляем метку.' })}
        before={<Avatar size={16} style={orangeBackground}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
      >
      Открыто новое место
      </Snackbar>, Poll:[{option:a.answer_1,votes:0},{option:a.answer_2,votes:0},{option:a.answer_3, votes:0}],ball:this.state.ball+50});
          }else{
            if(this.state.patency == true){
              this.setState({Poll:data.data,ball:this.state.ball+10,popout: <Snackbar
            onClose={() => this.setState({ snackbar: null })}
            action={<Icon24Share />}
            onActionClick={() => this.sendHistory()}
            before={<Avatar size={16} style={orangeBackground}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
          >
          + одно место
          </Snackbar>});
            }else{this.setState({Poll:data.data})}
            }
        });
        setTimeout(()=>{
          this.setState({activePanelMap:'quest', selectedPlace:a, popout:null})},550);
    })}
    else{
      this.setState({errorGeo:<p style={{"color":"red"}}>Попробуйте еще раз</p> , popout:null})

    }}

    setPoll=(a, e)=>{this.setState({PollEnd:a})}

     setShowEnd=(e)=>{
       this.setState({showEnd:true});
     }

     competionQuest=(e)=>{
       if(this.state.patency == true){
       axios.post("https://www.questsinpocket.com/vk/index.php?type=set_poll&id_marker="+this.state.selectedPlace.id_marker+"&poll="+this.state.PollEnd);
       axios.post("https://www.questsinpocket.com/vk/index.php?type=set&id_vk="+this.state.fetchedUser.id+"&ball="+this.state.ball);
       axios.post("https://www.questsinpocket.com/vk/index.php?type=set_quest&id_vk="+this.state.fetchedUser.id+"&id_marker="+this.state.selectedPlace.id_marker);
     }

       this.setState({activePanelMap:'map'})

     }

    sendHistory(){

    }

    openDefault(e) {
    this.setState({ popout:
      <Alert
        actions={[{
          title: 'Отмена',
          autoclose: true,
          style: 'cancel'
        }, {
          title: 'Ок',
          autoclose: true,
        }]}
        onClose={this.closePopout}
      >

        <p>Для стабильной работы включите геолокацию</p>
      </Alert>
    });
  }

  closePopout=(e)=> {this.setState({ popout: null})}

  onStoryChange =(e)=> {
    this.setState({ activeStory: e.currentTarget.dataset.story });
  }

  onToChange =(e)=> {
     this.setState({ activePanel: e.currentTarget.dataset.to});

  }

  render(){
    return (
      <Epic activeStory={this.state.activeStory} tabbar={
        <Tabbar>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'profile'}
            data-story="profile"
          ><Icon28User /></TabbarItem>
          <TabbarItem
            onClick={this.onClickMap}
            selected={this.state.activeStory === 'map'}
            data-story="map"

          ><Icon28Place/></TabbarItem>
          <TabbarItem
            onClick={this.onClickOther}
            selected={this.state.activeStory === 'other'}
            data-story="other"
          ><Icon28MoreHorizontal /></TabbarItem>
        </Tabbar>}>

      <View id={'profile'} popout={this.state.popout} activePanel={'profile'}>
        <Panel id={"profile"}>
          <Profile fetchedUser={this.state.fetchedUser} ball={this.state.ball} a={this.state.a} />
            {this.state.snackbar}
        </Panel>
      </View>
      <View id={'load'} popout={this.state.popout} activePanel={'load'}>
        <Panel id={"load"}>
          {this.state.snackbar}
        </Panel>
      </View>
      <View id={'map'} header={true} popout={this.state.popout} activePanel={this.state.activePanelMap}>
        <Panel id={'map'}>
        <PanelHeader
            left={<Tooltip
                text="Нажмите, чтобы обновить местоположение"
                isShown={this.state.tooltip}
                onClose={() => this.setState({ tooltip: false })}
                offsetX={10}
              >
                <HeaderButton onClick={() => this.updateGeodata()} ><Icon24Replay /></HeaderButton>
              </Tooltip>}
               addon={<HeaderButton onClick={() => this.updateGeodata()}>Обновить</HeaderButton>}>
            Карта
          </PanelHeader>
          <MapContainer geoData={this.state.geoData} updateGeodata={this.updateGeodata} errorGeo={this.state.errorGeo} checkGeo={this.checkGeo} marker={this.state.marker}/>
        </Panel>
        <Panel id={'quest'}>
          <Begin selectedPlace={this.state.selectedPlace} />
          <Poll selectedPlace={this.state.selectedPlace} Poll={this.state.Poll} setPoll={this.setPoll} setShowEnd={this.setShowEnd} />
          <End selectedPlace={this.state.selectedPlace} theEnd={this.competionQuest} showEnd={this.state.showEnd} />
          {this.state.snackbar}
        </Panel>
      </View>

      <View id={'other'} popout={this.state.popout} activePanel={'other'}>
        <Panel id={'other'}>
        <PanelHeader>
         Разное
        </PanelHeader>
          <Questions rr={this.state.rr} quest={this.state.questList} fetchedUser={this.state.fetchedUser} ball={this.state.ball} />
            {this.state.snackbar}
        </Panel>

      </View>
      </Epic>)
  }
}

export default App;
